.tui-page-btn.tui-first {
    display: none;
}
.tui-page-btn.tui-last {
    display: none;
}

.tui-page-btn.tui-prev-is-ellip.tui-first-child,
.tui-page-btn.tui-next-is-ellip.tui-last-child {
    border: none;
}

.tui-page-btn.tui-next {
    background-image: url(/src/images/arrow-next.png);
    background-position: 50%;
    background-repeat: no-repeat;
    border: none;
    margin-right: 0;
}

.tui-page-btn.tui-prev {
    background-image: url(/src/images/arrow-prev.png);
    background-position: 50%;
    background-repeat: no-repeat;
    border: none;
    margin-right: 0;
}

